.searchBox{
	background: #fff;
	text-align: center;
	font-size: 14pt;
	width: 25%;
	height: 30px;
	margin-top: 80px;
	border: none;
	outline: none;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.searchboxContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}
::-webkit-input-placeholder{
	text-align: center;
	color: black;
}
:-ms-input-placeholder{
	text-align: center;
	color: black;
}
::-ms-input-placeholder{
	text-align: center;
	color: black;
}
::placeholder{
	text-align: center;
	color: black;
}



